.searchbar{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    padding: 15px 0 10px;
}

.searchbar_input{
    border: none;
    width: 500px;
    height: 50px;
    border-radius: 20px;
    text-align: center;
    outline: none;
    font-family: $Nunito;
    font-size: 25px;
    margin: 20px;
    box-shadow: 0 10px 5px rgba(rgb(0, 0, 0), 0.3);
    transition: 0.4s;

    @media (max-width: 700px) {
        width: 75vw;
        font-size: 4.5vw;
    }
}

.searchbar_input:focus{
    animation: pulseInput 2.5s infinite;
    transform: scale(1.05);
    transition: 0.4s;
}

@keyframes pulseInput {
	0% {
		box-shadow: 0 10px 5px rgba(rgb(0, 0, 0), 0.3);
	}

	50% {
		box-shadow: 0 0 20px 8px rgba(212, 212, 212, 0.3);
	}

	100% {
		box-shadow: 0 10px 5px rgba(rgb(0, 0, 0), 0.3);
	}
}

.searchbar_submit{
    height: 40px;
    width: 250px;
    border: none;
    border-radius: 10px;
    margin: 3px 20px 40px 20px;
    outline: none;
    cursor: pointer;
    background-color: rgb(247, 247, 247);
    font-family: $Nunito;
    font-size: 20px;
    font-weight: 600;
    box-shadow: 0 5px 5px rgba(rgb(0, 0, 0), 0.3);
    transition: 1s;

    @media (max-width: 600px) {
        font-size: 4vw;
        width: 50vw;
    }
}

.searchbar_submit:hover{
    box-shadow: 0 0 20px rgba(59, 139, 65, 1.0);
    transition: 1s;
    animation: pulseSubmit 1.3s infinite;
}

@keyframes pulseSubmit {
	0% {
		box-shadow: 0 0 0 0 rgb(46, 163, 54);
	}

	70% {
		box-shadow: 0 0 15px 15px rgba(59, 139, 65, 0);
	}

	100% {
		box-shadow: 0 5px 5px rgba(rgb(0, 0, 0), 0.3);
	}
}

.searchbar_submit:active{
    background-color: rgb(70, 218, 80);
    transition: 0.25s;
    transform: translate(0, 2px);
}

.searchbar_start{
    display: flex;
    justify-content: center;
    text-transform: uppercase;
    margin: 10px 40px;
    font-family: $Abel;
    font-size: 18px;
    margin-top: 20px;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: rgb(163, 163, 163);
    text-align: center;
}
.bottomline{
    margin-bottom: 80px;
}

.searchbar_start-title{
    color: rgb(77, 177, 82);
}

.searchbar_error{
    display: flex;
    justify-content: center;
    text-transform: uppercase;
    margin: 10px 40px 30px;
    font-family: $Abel;
    font-size: 16px;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: rgb(209, 60, 60);
    text-align: center;
    transition: 1s;
    animation: animateError 0.5s;
}
@keyframes animateError {
    from {opacity: 0}
    to {opacity: 1}
}

.searchbar_results{
    display: flex;
    justify-content: center;
    margin: 10px 0 40px;
    font-family: $Abel;
    font-size: 15px;
    margin-top: 20px;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: rgb(163, 163, 163);
    text-align: center;

    @media (max-width: 700px) {
        width: 60vw;
        margin: 10px;
    }
}

//--------------------

.list_container{
    display: flex;
    justify-content: center;
}

.track_list{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.track_list-button{
    height: 40px;
    width: 250px;
    border: none;
    border-radius: 20px;
    margin: 35px 20px 20px 20px;
    outline: none;
    cursor: pointer;
    background-color: rgb(247, 247, 247);
    font-family: $Nunito;
    font-size: 17px;
    font-weight: 600;

    &.end{
        background-color: rgb(223, 60, 60);
        color: white;
        transition: 0.3s;
    }
}

.track_list-button:active{
    box-shadow: 0 0 8px 1px rgba(54, 182, 62, 0.9);
    background-color: rgb(240, 255, 243);
    transition: 0.1s;
    transform: translate(0, 2px);

    &.end{
        background-color: rgb(255, 0, 0);
        box-shadow: 0 0 15px rgb(216, 34, 34);  
        transition: 0.2s; 
    }
}


 